html {
  box-sizing: border-box;
  background-image: url(/currency-converter-with-react/static/media/currencyCalculatorBackground4.3d74795a.jpg);
  background-size: 100%; 
}

*,
::after, ::before {
  box-sizing: inherit;
}
.span {
    display: inline-block;
    padding: 20px 0 20px 20px;
} 
.clock {
    display: block;
    font-size: small;
    text-align: right;
}
.form__fieldset {
    max-width: 650px;
    margin: auto;
    padding-top: 15px;
    padding-bottom: 20px;
    opacity: 0.7;
}

.form__legend {
    font-size: x-large;
    letter-spacing: 7px;
    text-transform: uppercase;
}

.form__border {
    background-color: hsl(0, 0%, 100%);
    border-style: double;
    border-color: black;
    border-radius: 6px;
    border-width: 7px;
}

.form__text {
    display: inline-block;
    padding: 10px 0 10px 10px;
}

.form__input {
    margin: 10px 0 10px 10px;
    width: 150px;
}

.form__result {
    font-size: large;
}

.form__button {
    
    border-radius: 6px;
    width: 100%;
    max-width: 650px;
    background-color: hsl(0, 0%, 100%);
    opacity: 0.7;
    display: flex;
    justify-content: center;
    margin: 5px auto ;  
}

.form__button:hover {
    background-color: hsl(0, 3%, 19%);
}

.form__button:active {
    background-color: hsl(48, 51%, 89%);
}
.mainContainer {
  padding: 40px 20px;
  font-family: 'Archivo Black', sans-serif;
}
